import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faMicrophone, faMicrophoneSlash, faHeadphones, faVolumeOff } from '@fortawesome/free-solid-svg-icons'

export default class VCMember extends Component {



  render() {
    return (

    <a onClick={()=>{this.props.selectMember(this.props.details.id)}}>
      <li>
        <img src={this.props.details.avatar_url}/>
        <strong>{this.props.details.bot ? "Bot: " : "User: "}</strong>
        <span>{this.props.details.username}</span>
        {this.props.details.suppress ? <FontAwesomeIcon icon={faMicrophoneSlash} color={"gray"}/> : this.props.details.self_mute ? <FontAwesomeIcon icon={faMicrophoneSlash} /> : <FontAwesomeIcon icon={faMicrophone}/>}
        {this.props.details.suppress ? <FontAwesomeIcon icon={faVolumeOff} color={"gray"} /> : this.props.details.self_VolumeOff ? <FontAwesomeIcon icon={faVolumeOff}/> : <FontAwesomeIcon icon={faHeadphones}/>}
        {this.props.details.mute ? <FontAwesomeIcon icon={faMicrophoneSlash}  color={"red"}/> : <span></span>}
        {this.props.details.VolumeOff ? <FontAwesomeIcon icon={faVolumeOff} color={"red"}/> : <span></span>}
      </li>
    </a>
    )
  }
}


