import React from 'react';
import styled from 'styled-components';
import { snTimeStamp }      from '../util/functions';

const StyleWrapper2 = styled.div`
display: grid;
grid-template-columns: 256px 1fr;
height: 256px;
outline: 1px solid #777;
position: relative;
box-shadow: 0 2px 2px #747F8D;
background-color: #fff;
  img {
    width: 256px;
    height: 256px;
  }
  table {
    padding: 10px;
    font-size: 14px;
    table-layout: fixed;
    height: 256px;
    tbody{
      outline: 1px solid #eee;
    }
    td,th {
      padding: 5px;
    }
    tr {
    }
    tr:nth-child(odd) {
      background-color: #eee;
    }
    td {
      outline-left: 1px solid #fff;
      &:first-child {
        width: 180px;
      }
    }
  }
`;

const MemberDetails = ({member}) => {

  return (
    <StyleWrapper2>
      <img src={member ? member.avatar_url : "https://cdn.discordapp.com/embed/avatars/0.png"}></img>
      <table >
        <tbody>
          <tr>
            <td>Username:</td>
            <td>{member ? member.username : null}</td>
          </tr>
          <tr>
            <td>Nickname:</td>
            <td>{member ? member.nick : null}</td>
          </tr>
          <tr>
            <td>ID:</td>
            <td>{member ? member.id : null}</td>
          </tr>
          <tr>
            <td>Discriminator:</td>
            <td>{member ? member.discriminator : null}</td>
          </tr>
          <tr>
            <td>Game:</td>
            <td>{member ? (member.game !== undefined ? member.game.name : '') : null}</td>
          </tr>
          <tr>
            <td>Status:</td>
            <td>{member ? member.status : null}</td>
          </tr>
          <tr>
            <td>Account Type:</td>
            <td>{member ? (member.bot ? "Bot" : "User"): null}</td>
          </tr>
          <tr>
            <td>Account Created:</td>
            <td>{member ? snTimeStamp(member.id) : null}</td>
          </tr>
        </tbody>
      </table>
    </StyleWrapper2>
  )
}
export default MemberDetails;


