import React, { Component } from 'react';
import styled               from 'styled-components';
import ServerDetails        from './container/ServerDetails'
import MemberDetails        from './container/MemberDetails';
import MembersList          from './container/MembersList';
import ChannelsList         from './container/ChannelsList';
import { snTimeStamp }      from './util/functions';

const Grid = styled.main`
  display: grid;
  grid-template-columns: 1fr 350px;
  grid-template-rows: 150px 256px 1fr;
  grid-gap: 10px;
  padding: 15px;
  max-width: 1200px;
  margin: 0 auto;
`

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: {
        status: null,
        serverContent: {
          members: []
        },
        dateCreated: null
      },
      currentMember: []
    }
  }

  getServerDetails = (serverId) => {
    this.setState({
      response: {
        status: null,
        serverContent: null,
        dateCreated: null
      },
      currentMember: []
    })
    const xhr = new XMLHttpRequest()
    xhr.onload = () => {
      switch(xhr.status) {
        case 200:
          this.setState({
            response: {
              status: xhr.status,
              serverContent: JSON.parse(xhr.responseText),
              dateCreated: snTimeStamp(serverId)
            }
          })
          break;
        case 403:
          this.setState({
            response: {
              status: xhr.status,
              serverContent: {
                members: []
              },
              dateCreated: snTimeStamp(serverId)
            }
          })
        break;
        default:
          this.setState({
            response: {
              status: xhr.status,
              serverContent: {
                members: []
              },
              dateCreated: null
            }
          })
          break;
      }
    }
    xhr.open('GET', `https://discordapp.com/api/guilds/${serverId}/widget.json`)
    xhr.send()
  }

  setMemberDetails = (id) => {
    this.setState({
      currentMember: this.state.response.serverContent.members.filter(member => member.id === id)
    });
  }

  membersInVC = (channelId) => {
    return this.state.response.serverContent.members.filter(member => channelId === member.channel_id)
  }

  VCMemCount = () => this.state.response.serverContent.members.filter(member => member.channel_id).length

  render() {
    return (
      <Grid>
        <ServerDetails lookup={this.getServerDetails} response={this.state.response}/>
        <MembersList status={this.state.response.status} members={this.state.response.serverContent ? this.state.response.serverContent.members : null} selectMember={this.setMemberDetails} />
        <MemberDetails member={this.state.currentMember[0]}/>
        <ChannelsList
          memCount={this.VCMemCount}
          renderMembers={this.membersInVC}
          channels={this.state.response.serverContent ? this.state.response.serverContent.channels : null}
          selectMember={this.setMemberDetails}/>
      </Grid>
    );
  }
}

export default App;



