import React, { Component } from 'react';
import styled from 'styled-components';
import Member from '../presentational/Member';
import { FadeLoader } from 'react-spinners';
import { css } from 'react-emotion';

const override = css`
    display: block;
    margin: auto;
    margin-top: 42vh;
    border-color: red;
`;

const StyleWrapper = styled.div`
  grid-row: span 3;
  outline: 1px solid #333;
  padding: 10px;
  max-width: 350px;
  display: grid;
  grid-gap: 5px;
  box-shadow: 0 2px 2px #747F8D;
  background-color: #fff;
  ul {
    list-style: none;
    margin:0;
    padding:5px 0;
    height: calc(100vh - 82px);
    overflow-x: hidden;
    overflow-y: scroll;
    outline: 1px solid #999;
    box-shadow: inset 0 0 2px #747F8D;
    a {
      cursor: pointer;
      &:nth-child(odd) {
        li {
          background-color: #fafafa;
        }
      }
    }
    li {
      display: grid;
      grid-template-columns: 35px 0 35px 1fr;
      align-items: center;
      grid-gap: 10px;
      margin: 0 5px;
      padding: 2px;
      &:hover {
        outline: 1px solid blue;
        box-shadow: 0 2px 2px #999;
      }
      img {
        width: 35px;
        height: 35px;
        border-radius: 50%;
      }
      div {
        margin-left: -20px;
        margin-top: 25px;
        width: 10px;
        height: 10px;
        border: 1px solid #fff;
        border-radius: 10px;
        &.online {
          background-color: #43B581;
        }
        &.idle {
          background-color:  #F9A51A;
        }
        &.dnd {
          background-color: #EF4747;
        }
      }
    }
  }
`;

export default class MembersList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false
    }
  }



  childrenLoaded = () => {
    this.setState({
      loading: false
    })
  }

  render() {
    return (
      <StyleWrapper>
        <span>Members Online: {this.props.members !== null ? (
          this.props.members.length
        ):(
          null
        )}</span>
        <ul>
          { this.props.members === null ? (
          <FadeLoader
            className={override}
            sizeUnit={"px"}
            size={150}
            color={'#123abc'}
            loading={true}
          />
          ):(null) }
          {this.props.members !== null ? (
            this.props.members.map((member, i) => <Member details={member} selectMember={this.props.selectMember} lastComponent={(i === (this.props.members.length - 1)) ? this.childrenLoaded : false}/>)
          ):(
            null
          )}
        </ul>
      </StyleWrapper>
    )
  }
}



