import React from 'react';
import { snTimeStamp } from '../util/functions';
import VCMember from './VCMember';

const Channel = ({details, renderMembers, selectMember}) => {
  return (
    <div className="channel">
      <div className="channel-title">
        <span>{details.name}</span>
        <span>Channel Created: {snTimeStamp(details.id)}</span>
      </div>
      {renderMembers(details.id).length > 0 ? (
        <ul>
          {renderMembers(details.id).map(member => <VCMember details={member} selectMember={selectMember}/>)}
        </ul>
      ):(null)}
    </div>
  )
}

export default Channel



