import React, { Component } from 'react';
import styled from 'styled-components';

const StyleWrapper = styled.div`
  outline: 1px solid #333;
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 300px;
  padding: 10px;
  box-shadow: 0 2px 2px #747F8D;
  background-color: #fff;
  h1 {
    font-size: 300%;
    text-align: center;
    color: #7289DA;
    text-shadow: 0 8px 14px rgba(0,0,0,.2);
  }
  form {
    input {
      text-align: center;
      width: 290px;
      border: none;
      outline: 1px solid #999;
      padding: 5px;
      &:focus {
        outline: 1px solid #999;
      }
    }
    input[type="number"]::-webkit-outer-spin-button, input[type="number"]::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;}
    input[type="number"] {
        -moz-appearance: textfield;
    }
    button {
      background-color: #e6e6e6;
      width: 300px;
      border:none;
      outline: 1px solid #777;
      margin: 5px auto;
      padding: 5px;
      cursor: pointer;
      &:hover {
        outline: 1px solid #00f;
        background-color: #ddf;
      }
    }
  }
  dl {
    outline: 1px solid #ccc;
    padding: 3px 10px;
    height: 60px;
    margin: 0;
  }
`;

export default class ServerDetails extends Component {
  constructor(props){
    super(props);
    this.state = {
      inputValue: null
    }
  }

  handleInput = (e) => {
    this.setState({
      inputValue: e.target.value
    })
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.props.lookup(this.state.inputValue)
  }

  test = (status) => {
    switch(status) {
      case 200:
        return <dd style={{color:'green'}}>Enabled</dd>;
      case 403:
        return <dd style={{color:'red'}}>Disabled</dd>;
      default:
        return <dd>Unknown Server</dd>;
    }
  }

  render() {
    return (
      <StyleWrapper>
        <h1>Discord Widget Scanner</h1>
        <div>
          <form onSubmit={this.handleSubmit}>
            <input placeholder="Enter Server ID" type="number" onChange={this.handleInput}/>
            <button>Look Up</button>
          </form>
          <dl>
            <dt>Widget Status: </dt>
              {this.test(this.props.response.status)}
            <dt>Date Created: </dt>
              <dd>{this.props.response.dateCreated}</dd>
          </dl>
        </div>
      </StyleWrapper>
    )
  }
}


