import React, { Component } from 'react';
import Channel from '../presentational/Channel';
import styled from 'styled-components';
import { FadeLoader } from 'react-spinners';
import { css } from 'react-emotion';

const override = css`
    display: block;
    margin: auto;
    margin-top: 20vh;
    border-color: red;
`;

const StyleWrapper = styled.div`
    outline: 1px solid #333;
    padding: 10px;
    background-color: #fff;
    box-shadow: 0 2px 2px #747F8D;
  .channels{
    outline:1px solid #999;
    padding: 0 5px;
    height: calc(100vh - 516px);
    list-style: none;
    overflow-x: hidden;
    overflow-y: scroll;
    box-shadow: inset 0 0 2px #747F8D;
  }
  .vc-count {
    margin-bottom: 10px;
    display: block;
  }
  .channel {
    display: grid;
    outline: solid 1px #aaa;
    /* box-shadow: 0 2px 2px #747F8D; */
    padding: 10px;
    margin: 4px auto;
    .channel-title {
      display: grid;
      grid-template-columns: 200px 1fr;
    }
    ul {
      a {
      cursor: pointer;
      &:nth-child(odd) {
        li {
          background-color: #eee;
        }
      }
    }
    li {
      display: grid;
      grid-template-columns: 20px 35px 200px repeat(4, 20px);
      align-items: center;
      grid-gap: 10px;
      margin: 0 5px;
      padding: 2px;
      &:hover {
        outline: 1px solid blue;
        box-shadow: 0 2px 2px #999;
      }
      img {
        width: 20px;
        height: 20px;
        border-radius: 50%;
      }
      /* div {
        margin-left: -20px;
        margin-top: 25px;
        width: 10px;
        height: 10px;
        border: 1px solid #fff;
        border-radius: 10px;
        &.online {
          background-color: #43B581;
        }
        &.idle {
          background-color:  #F9A51A;
        }
        &.dnd {
          background-color: #EF4747;
        }
      } */
    }
    }
  }
`;

export default class ChannelsList extends Component {
  render() {
    return (
      <StyleWrapper>
        <span className="vc-count">Visible Voice Channel Members: {this.props.channels ? this.props.memCount() : 0} </span>
        <div className="channels">
        { this.props.channels === null ? (
          <FadeLoader
            className={override}
            sizeUnit={"px"}
            size={150}
            color={'#123abc'}
            loading={true}
          />
          ):(null) }
          {this.props.channels ? this.props.channels.sort( (c1,c2) => c1.position > c2.position ? 1 : -1 )
            .map(channel => <Channel renderMembers={this.props.renderMembers} details={channel} selectMember={this.props.selectMember}/>) : null}
        </div>  
      </StyleWrapper>
    )
  }
}



